.App {
  text-align: center;
}

.card {
  display: flex;
  flex-direction: column;
  border-radius: 0.75rem;
  width: 100px;
  box-shadow: 0 1px 15px rgba(0, 0, 0, 0.04), 0 1px 6px rgba(0, 0, 0, 0.04);
  padding: 32px 8px;
  align-items: center;
}

.cardTitle {
  color: #8f8f8f;
  height: 30px;
  line-height: 26px;
  text-transform: capitalize;
}

.cardValue {
  font-size: 1.8rem;
  font-weight: 300;
  line-height: 2rem;
  color: #004fef;
}

.dashboardContent {
  display: flex;
  width: 500px;
  justify-content: space-around;
  margin: auto;
}

.temperatureIcon {
  width: 55px;
  height: 55px;
  background-image: url("./assets/images/temperature-svgrepo-com.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.humidityIcon {
  width: 55px;
  height: 55px;
  background-image: url("./assets/images/humidity-svgrepo-com.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.airPressureIcon {
  width: 55px;
  height: 55px;
  background-image: url("./assets/images/gauge-pressure-svgrepo-com.svg");
  background-position: center;
  background-repeat: no-repeat;
}
